import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.page.html',
  styleUrls: ['./inicio.page.scss'],
})
export class InicioPage implements OnInit {

  role = '';

  constructor(
    private almacenamiento: InicioService
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.datos();
  }

  async datos() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      this.role = info.role;
    }
  }

}
